import React, { useState } from "react";
import GradientBar from "../common/GradientBar";
import Card from "../common/Card";
import { Form, Formik } from "formik";
import FormSuccess from "../FormSuccess";
import FormError from "../common/FormError";
import FormInput from "../FormInput";
import Label from "../common/Label";
import GradientButton from "../common/GradientButton";
import * as Yup from "yup";
import { publicFetch } from "../../util/fetch";

export default function AddProduct(props) {
  const AddProductSchema = Yup.object().shape({
    name: Yup.string().required("Product name is required"),
    description: Yup.string().required("Description is required"),
    price: Yup.string().required("Price is required"),
  });
  const [addProductSuccess, setProductSuccess] = useState();
  const [addProductError, setProductError] = useState();
  const [imagePreview, setImage] = useState("");
  const [imageType, setImageType] = useState();
  const [isSelected, setIsSelected] = useState(false);

  const [addLoading, setLoginLoading] = useState(false);

  const submitValues = async (values) => {
    try {
      setLoginLoading(true);

      values.imageType = imageType;

      values.image = imagePreview;

      values.owner = props.owner;

      const { data } = await publicFetch.put(
        `products/${props.product._id}`,
        values
      );

      setProductSuccess(data.message);
      setProductError("");

      setTimeout(() => {
        window.location.reload();
      }, 10);
    } catch (error) {
      setLoginLoading(false);
      const { data } = error.response;
      setProductError(data.message);
      setProductSuccess("");
    }
  };

  const changeHandler = (event) => {
    const file = event.target.files[0];
    setIsSelected(true);
    getBase64(file);
  };

  const onLoad = (fileString) => {
    setImage(fileString);
    const typeImg = fileString.split(",");
    setImageType(typeImg[0]);
  };

  const getBase64 = (file) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => {
      onLoad(reader.result);
    };
  };
  let initialValues = {
    name: props.product ? props.product.name : "",
    description: props.product ? props.product.description : "",
    price: props.product ? props.product.price : "",
    category: props.product ? props.product.category : "Uniformes",
  };

  return (
    <>
      <section className="w-3/4 h-screen m-auto sm:pt-10">
        <GradientBar />
        <Card>
          <div className="flex items-center justify-center py-7 px-2 sm:px-3 lg:px-3">
            <div className="max-w-md w-full flex items-center justify-center">
              <Formik
                initialValues={initialValues}
                onSubmit={(values) => submitValues(values)}
                validationSchema={AddProductSchema}
              >
                {() => (
                  <Form className="mt-8">
                    {addProductSuccess && (
                      <FormSuccess text={addProductSuccess} />
                    )}
                    {addProductError && <FormError text={addProductError} />}
                    <input type="hidden" name="remember" value="true" />
                    <div>
                      <div className="mb-2 mr-2 w-full">
                        <div className="mb-1">
                          <Label text="Image" />
                        </div>
                          <div className="flex justify-center mb-3">
                            <img
                              src={props.product.image}
                              className="w-32 h-32 "
                              alt=""
                            />
                          </div>
                        <div className="file-select" id="src-file">
                          <input
                            type="file"
                            onChange={changeHandler}
                            className="inline-block relative"
                          />
                        </div>
                      </div>
                      <div className="flex">
                        <div className="mb-2 mr-2 w-1/2">
                          <div className="mb-1">
                            <Label text="Name" />
                          </div>
                          <FormInput
                            ariaLabel="Name"
                            name="name"
                            type="text"
                            placeholder="Name"
                          />
                        </div>
                        <div className="mb-2 ml-2 w-1/2 ">
                          <div className="mb-1">
                            <Label text="Price (cop) $:" />
                          </div>
                          <FormInput
                            ariaLabel="Price"
                            name="price"
                            type="number"
                            placeholder="Price"
                          />
                        </div>
                      </div>
                      <div className="mb-2 w-full">
                        <div className="mb-1">
                          <Label text="Description" />
                        </div>
                        <div>
                          <FormInput
                            ariaLabel="Description"
                            name="description"
                            type="text"
                            placeholder="Description"
                          />
                        </div>
                      </div>
                    </div>
                    <div className="mt-6 flex items-center justify-center font-nucleo">
                      <GradientButton
                        type="submit"
                        text="Save"
                        loading={addLoading}
                      />
                    </div>
                  </Form>
                )}
              </Formik>
            </div>
          </div>
        </Card>
      </section>
    </>
  );
}
