import React, { useState, useEffect, useContext } from "react";
import PageTitle from "../components/common/PageTitle";
import { publicFetch } from "../util/fetch";
import ProductCard from "../components/products/productCardAdmind";
import AddProduct from "../components/products/AddProduct";
import EditProduct from "../components/products/EditProduct";
import Button from "../components/common/GradientButton";
import GradientBar from "../components/common/GradientBar";
import { AuthContext } from "../context/AuthContext";
import SearchInput from "../components/common/SearchInput";
import { useProducts } from "../context/ProductContext";

export default function Dashboard(props) {
  //   const [products, setProductInfo] = useState([]);
  const [search, setSearch] = useState(false);
  const [showAdd, setShowAdd] = useState(false);
  const [showEdit, setShowEdit] = useState({ show: false, product: undefined });
  const { products, setProductInfo } = useProducts();

  const auth = useContext(AuthContext);
  const { authState } = auth;

  useEffect(() => {
    const listUsers = async () => {
      const { data } = await publicFetch.get(`products`);

      setProductInfo({ action: "define", payload: data.products });
    };
    if (!search) {
      listUsers();
    }
  }, []);

  const showAddPanel = () => {
    setShowAdd(!showAdd);
    setShowEdit(false);
  };
  const showEditPanel = (product) => {
    setShowEdit({ show: true, product });
    setShowAdd(false);
  };

  const searchProduct = async (value) => {
    try {
      const { data } = await publicFetch.get(`products/${value.search}`);

      if (data.products.length === 0) {
        alert("No results");

        const { data } = await publicFetch.get(`products`);

        setSearch(true);
        setProductInfo({ action: "define", payload: data.products });
      } else {
        setSearch(true);
        setProductInfo({ action: "define", payload: data.products });
      }
    } catch (error) {
      setSearch(true);
      console.log(error);
    }
  };
  return (
    <>
      <div className="container mx-auto px-4">
        <PageTitle title="Dashboard" />

        <div className="flex mt-6 font-serif">
          <SearchInput searchProduct={searchProduct} />
        </div>
        <div className="-mx-4 sm:-mx-8 px-4 sm:px-8 py-4 overflow-x-auto">
          <div className="container-add font-nucleo">
            <Button text="Add" onClick={showAddPanel} />
          </div>
          <div style={{ display: `${showAdd ? "block" : "none"}` }}>
            <AddProduct
              showPanel={showAddPanel}
              owner={authState.userInfo.identification}
            />
          </div>
          <>
            {showEdit.show && showEdit.product ? (
              <div
                style={{
                  display: "block",
                }}
              >
                <EditProduct
                  showPanel={showEditPanel}
                  owner={authState.userInfo.identification}
                  product={showEdit.product}
                />
              </div>
            ) : null}
          </>
          <div className="mt-10">
            <GradientBar />
            <div className="flex items-center mt-5 sm:mt-10">
              <div className="grid grid-cols-1 gap-5 sm:grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4">
                {products && products.length
                  ? products.map((pd) => (
                      <ProductCard
                        key={pd._id}
                        product={pd}
                        owner={authState.userInfo.firstName}
                        onClickEdit={showEditPanel}
                      />
                    ))
                  : null}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
